import React from 'react';

// Définition du composant WebViewerWidget
const WebViewerWidget = ({ url, width = '100%', height = '500px' }) => {
  return (
    <div style={{ width, height, border: 'none', overflow: 'hidden' }}>
      <iframe
        src={url}
        width="100%"
        height="100%"
        style={{ border: 'none' }}
        title="Web Viewer"
        allowFullScreen
      ></iframe>
    </div>
  );
};

// Composant App qui utilise WebViewerWidget pour afficher le site
const App = () => {
  return (
    <div>
      <h2>Site Web Incorporé</h2>
      <WebViewerWidget url="https://chat.openai.com" />
    </div>
  );
};

export default App;
